/* Single posts navigation styles - begin
    ====================================================================== */

.mkdf-blog-single-navigation {
    position: relative;
    width: calc(100% - 110px);
    float: right;
    vertical-align: middle;
    margin-bottom: 20px;
    padding: 0;
    clear: both;
    box-sizing: border-box;
    border-top: 1px solid $default-border-color;
    border-bottom : 1px solid $default-border-color;

    .mkdf-blog-single-navigation-inner {
        padding: 6px 0 9px;
        font-size: 14px;
        font-weight: 500;
        text-transform: capitalize;
    }

    .mkdf-blog-single-prev,
    .mkdf-blog-single-next {

        color: $default-text-color;

        .mkdf-blog-single-nav-mark {
            font-size: 14px;
            line-height: inherit;
            color: inherit;

            &:before {
                display: block;
                line-height: inherit;
            }
        }

        .mkdf-blog-single-nav-label {
            color: inherit;
        }

        &:hover {
            color: $first-main-color;
        }
    }

    .mkdf-blog-single-prev {
        float: left;

        .mkdf-blog-single-nav-mark,
        .mkdf-blog-single-nav-label {
            float: left;
        }

        .mkdf-blog-single-nav-mark {
            margin: 0 6px 0 0;
        }
    }

    .mkdf-blog-single-next {
        float: right;

        .mkdf-blog-single-nav-mark,
        .mkdf-blog-single-nav-label {
            float: right;
        }

        .mkdf-blog-single-nav-mark {
            margin: 0 0 0 6px;
        }
    }
}

.mkdf-single-links-pages {
    clear: both;
    margin: 20px 0 5px;

    .mkdf-single-links-pages-inner {

        > a,
        > span {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            margin: 0 15px 5px 0;
        }
    }
}
/* Single posts navigation styles - end
    ====================================================================== */