/* ==========================================================================
   Blog List shortcode - Boxed type style - begin
   ========================================================================== */

.mkdf-blog-list-holder {
	
	&.mkdf-bl-boxed {
		
		&:not(.mkdf-bl-one-column) {
			
			.mkdf-bl-item {
				float: left;
			}
		}
		
		$columns_number: ('two', 'three', 'four', 'five');
		
		@for $i from 0 to length($columns_number) {
			&.mkdf-bl-#{nth($columns_number,$i+1)}-columns {
				
				.mkdf-bl-item {
					width: 100% / ($i+2);
				}
				
				@if ($i > 1) { // set different break point for four and five columns
					@media only screen and (min-width: $laptop-landscape-plus-pixel) {
						
						.mkdf-bl-item {
							
							&:nth-child(#{$i+2}n+1) {
								clear: both;
							}
						}
					}
				} @else {
					@media only screen and (min-width: $ipad-landscape-plus-pixel) {
						
						.mkdf-bl-item {
							
							&:nth-child(#{$i+2}n+1) {
								clear: both;
							}
						}
					}
				}
			}
		}

		.mkdf-bli-inner {
			border: 1px solid $default-border-color;
			@include mkdfTransition(box-shadow .3s ease-in-out);

			&:hover {
				box-shadow: 0px 1px 23.28px 0.72px rgba(255, 51, 102, 0.07);
				border: 1px solid #f8edf0;
			}
		}
		
		.mkdf-bli-content {
			padding: 25px 29px 8px;
			background-color: #fff;

			.mkdf-post-info-icon {
				vertical-align: middle;
				margin-right: 3px;
			}
		}

		.mkdf-post-image {
			margin: 0;
			@include mkdfTransition(all .3s cubic-bezier(0.4, 0.47, 0.2, 0.64));
		}

		.mkdf-bli-heading {
			position: relative;
			overflow:hidden;

			.mkdf-blog-list-share {
				position: absolute;
				top: 22px;
				right: 0px;
			}

			&:hover {
				.mkdf-post-image { 
					@include mkdfTransform(scale(1.05));
				}
			}
		}

		.mkdf-bli-info-bottom {
			margin-top: 18px;

			> div {
				display: inline-block;
				margin-right: 10px;

				&:last-child {
					margin-right: 0;
				}

				&:nth-child(2) {
					margin-right: 4px;
				}
			}

			.mkdf-post-info-author {

				a {
					color: $default-text-color;
					font-weight: 400;
					letter-spacing: -.02em;
					&:hover {
						color: $first-main-color;
					}

				}
			}

			.mkdf-blog-like {

				span,
				i {
					vertical-align: middle;
					color: $additional-text-color;
					&:first-child {
						margin-right: 7px;
						@include mkdfTransition(color 0.15s ease-out);
					}
				}

				.liked i {
					color: $first-main-color;
				}

				&:hover {

					i:first-child,
					span:first-child {
						color: $first-main-color;
					}
				}

				.mkdf-like {
					> span {
						position: relative;
						bottom: 2px;
					}
				}
			}

			.mkdf-post-info-comments-holder {
				span,
				i {
					vertical-align: middle;
					color: $additional-text-color;
				}

				.mkdf-post-info-icon {
					i {
						@include mkdfTransition(color 0.15s ease-out);
					}
					&:hover {
						i {
							color: $first-main-color;
						}
					}
				}

				.mkdf-post-info-comments {
					>span:not(.mkdf-post-info-icon) {
						position: relative;
						bottom: 2px;
					}
				}
			}

			.mkdf-post-info-category {
				.mkdf-post-info-icon {
					position: relative;
					display: inline-block;
					top: 2px;

					i {
						color: $default-text-color;
						@include mkdfTransition(color 0.15s ease-out);

						&:hover {
							i {
								color: $first-main-color;
							}
						}
					}
				}
				> a {
					color: $default-text-color;
					font-weight: 400;
					letter-spacing: -.02em;
					&:hover {
						color: $first-main-color;
					}
				}
			}
		}
	}
}
/* ==========================================================================
   Blog List shortcode - Boxed type style - end
   ========================================================================== */