/* ==========================================================================
   Blog List - Masonry style - begin
   ========================================================================== */

.mkdf-blog-holder {
	
    &.mkdf-blog-masonry {

        article {
            margin: 0 0 20px;
            padding: 0 0 30px;

            &.mkdf-post-has-media {
                .mkdf-post-heading {
                    margin-bottom: 20px;
                }
            }

            .mkdf-post-content {
                border: 1px solid $default-border-color;
                background-color: #fff;
                @include mkdfTransition(box-shadow .3s ease-in-out);

                &:hover {
                    box-shadow: 0 1px 23.28px 0.72px rgba(255, 51, 102, 0.07);
                    border: 1px solid #f8edf0;
                }
            }

            .mkdf-post-text {
                padding: 0 30px 4px;
            }

            .mkdf-post-title {
                margin: 0 0 15px;
            }

            .mkdf-post-excerpt-holder {
                @include mkdfRelativeHolderLayout();
                margin: 0 0 20px;

                p {
                    margin: 0;
                }
            }

            .mkdf-more-link-container {
                @include mkdfRelativeHolderLayout();
                margin: 20px 0 0;
            }

            .mkdf-post-info-top {
                margin-bottom: 8px;

                .mkdf-post-info-date {
                    display: inline-block;

                    .mkdf-post-info-icon {
                        position: relative;
                        display: inline-block;
                        top: 2px;
                        color: $additional-text-color;
                        font-size: 16px;
                        margin-right: 3px;
                    }

                    > a {
                        color: $additional-text-color;
                        font-weight: 400;
                        letter-spacing: -0.02em;
                        &:hover {
                            color: $first-main-color;
                        }

                    }
                }

            }


