            &.format-audio {
                &.mkdf-post-no-media.has-post-thumbnail {
                    .mkdf-post-heading {
                        margin-bottom: 20px;
                    }
                }
            }

            &.format-link {
	            
                .mkdf-post-text {
	                padding: 57px 25px 60px;
                }
	            
                .mkdf-post-text-main {
                    text-align: center;
                }
	            
                .mkdf-post-mark {
                    width: 100%;
                    text-align: center;

                    .mkdf-link-mark {
                        font-size: 36px;
                        color: $first-main-color;
                    }
                }
	            
                .mkdf-post-link-holder {
                    text-align: center;
                }
	            
                .mkdf-post-title {
                    margin-bottom: 0;
                    margin-top: 6px;
                }
            }
	        
            &.format-quote {
	            
                .mkdf-post-text {
	                padding: 35px 25px 40px;
                }
	            
                .mkdf-post-text-main {;
                    text-align: center;
                }
	            
                .mkdf-post-mark {
                    width: 100%;
                    vertical-align: top;
                    text-align: center;

                    .mkdf-quote-mark {
                        font-size: 48px;
                        color: $first-main-color;
                    }
                }
	            
                .mkdf-post-title {
                    margin-bottom: 10px;
                    margin-top: 9px;
                }

                .mkdf-quote-author {
                    letter-spacing: -.02em;
                }
            }
        }
    }
