/* ==========================================================================
   Blog List - Standard style - begin
   ========================================================================== */

.mkdf-blog-holder {

    &.mkdf-blog-standard {

        article {
            margin: 0 0 70px;
            padding: 0;

            &.mkdf-post-has-media {
                .mkdf-post-heading {
                    margin-bottom: 41px;
                }
            }

            .mkdf-post-info-left {
                float: left;
                width: 110px;
            }

            .mkdf-like-posts-enabled & {

                .mkdf-post-text-inner {
                    box-sizing: border-box;
                    float: right;
                    width: calc(100% - 110px);
                }
            }

            .mkdf-post-text-inner {
                box-sizing: border-box;
                float: none;
                width: 100%;
            }

            .mkdf-post-title {
                margin: 0 0 8px;
                padding-right: 10%;
            }

            .mkdf-post-excerpt-holder {
                @include mkdfRelativeHolderLayout();
            }

            .mkdf-more-link-container {
                @include mkdfRelativeHolderLayout();
                margin: 20px 0 0;
            }

            .mkdf-post-info-bottom {
	            padding-top: 21px;

                > div {
                    padding-right: 14px;

                    &:last-child {
                        padding-right: 0;
                    }
                }

                .mkdf-post-info-author {
                    display: inline-block;

                    .mkdf-post-info-author-image {
                        display: inline-block;
                        vertical-align: middle;
                        a {
                            display: block;
                        }

                        img {
                            display: block;
                            border-radius: 50%;
                        }
                    }
	                
                    .mkdf-post-info-author-link {
                        color: $default-heading-color;
	                    text-transform: capitalize;
                        font-weight: 500;
	                    
                        &:hover {
                            color: $first-main-color;
                        }
                    }
                }
                .mkdf-post-info-date {
                    display: inline-block;

                    .mkdf-post-info-icon {
                        position: relative;
                        display: inline-block;
                        top: 2px;
                        color: $additional-text-color;
                        font-size: 16px;
                        margin-right: 3px;
                    }

                    > a {
                        color: $additional-text-color;
                        font-weight: 400;
                        letter-spacing: -0.02em;
                        &:hover {
                            color: $first-main-color;
                        }

                    }
                }

                .mkdf-post-info-comments-holder,
                .mkdf-blog-like {
                    display: inline-block;

                    a {
                        display: inline-block;

                        span,
                        i {
                            vertical-align: middle;
                            color: $additional-text-color;
                        }
                    }
                }

                .mkdf-blog-like {
	                
                    i:first-child,
                    span:first-child {
                        margin-right: 7px;
	                    @include mkdfTransition(color 0.15s ease-out);
                    }

                    .liked i {
                        color: $first-main-color;
                    }

                    &:hover {
	                    
                        i:first-child,
                        span:first-child {
                            color: $first-main-color;
                        }
                    }

                    .mkdf-like {
                        > span {
                            position: relative;
                            bottom: 3px;
                        }
                    }
                }

                .mkdf-post-info-category {
                    display: inline-block;

                    .mkdf-post-info-icon {
                        position: relative;
                        display: inline-block;
                        top: 2px;

                        i {
                            color: $default-text-color;
                            @include mkdfTransition(color 0.15s ease-out);

                            &:hover {
                                i {
                                    color: $first-main-color;
                                }
                            }
                        }
                    }
                    > a {
                        color: $default-text-color;
                        font-weight: 400;
                        letter-spacing: -.02em;
                        &:hover {
                            color: $first-main-color;
                        }
                    }
                }

                .mkdf-post-info-comments-holder {
	                
                    span:first-child {
                        margin-right: 3px;
                        i {
                            @include mkdfTransition(color 0.15s ease-out);
                        }

                        &:hover {
                            i {
                                color: $first-main-color;
                            }
                        }
                    }

                    .mkdf-post-info-comments {
                        >span:not(.mkdf-post-info-icon) {
                            position: relative;
                            bottom: 3px;
                        }
                    }
                }
            }

            &.format-audio {
                &.mkdf-post-no-media.has-post-thumbnail {
                    .mkdf-post-heading {
                        margin-bottom: 50px;
                    }
                }
            }

            &.format-link {

                .mkdf-post-text-inner {
                    width: 100%;
                }
	            
                .mkdf-post-text{
	                padding: 60px 31px 20px 11%;
	                background-color: #fff;
                    border: 1px solid #f8edf0;
                }
	            
                .mkdf-post-text-main {
                    @include mkdfTableLayout();
                    text-align: left;
                }
	            
                .mkdf-post-mark {
                    position: relative;
                    width: 100%;
                    text-align: right;

                    .mkdf-link-mark {
                        padding-top: 5px;
                        font-size: 36px;
                        color: $first-main-color;
                    }
                }
	            
                .mkdf-post-title {
                    margin-bottom: 0;
                }
            }

            &.format-quote {

                .mkdf-post-text-inner {
                    width: 100%;
                }

                .mkdf-post-title {
                    padding-left: 0;
                }
	            
                .mkdf-post-text {
	                padding: 60px 31px 20px 11%;
	                background-color: #fff;
                    border: 1px solid #f8edf0;
                }
	            
                .mkdf-post-text-main {
                    @include mkdfTableLayout();
                    text-align: left;
                }

                .mkdf-quote-author {
                    font-weight: 500;
                    letter-spacing: -.03em;
                }
	            
                .mkdf-post-mark {
                    position: relative;
                    width: 100%;
                    text-align: right;
                    margin-top: -10px;

                    .mkdf-quote-mark {
                        padding-top: 5px;
                        font-size: 48px;
                        color: $first-main-color;
                    }
                }
	            
                .mkdf-post-link-holder {
                    @include mkdfTableCellLayout();
                    vertical-align: top;
                    text-align: left;
                }
	            
                .mkdf-post-title {
                    margin-bottom: 9px;
                }
	            
                .mkdf-post-author {
	                display: inline-block;
	                margin-bottom: 23px;
	                font-size: 13px;
	                line-height: 20px;
                }
            }
        }
    }
}

