/* Single author box styles - begin
	====================================================================== */

.mkdf-author-description {
    position: relative;
    display: inline-block;
    width: calc(100% - 110px);
    float: right;
    vertical-align: middle;
    padding: 40px 0;
    text-align: left;
    box-sizing: border-box;

    .mkdf-author-description-inner {
        min-height: 150px; // static height of author image -- mkdf-author-description-image
        clear: both;
    }

    .mkdf-author-description-content {
        position: relative;
        padding: 0 30px;
    }

    .mkdf-author-description-image {
        width: 200px;
        float: left;

        img {
            display: block;
            border-radius: 5em;
        }
        a {
            display: block;
        }
    }

    .mkdf-author-description-text-holder {
        position: relative;
        width: calc(100% - 200px);
        float: right;
        padding: 8px 0 4px;

        .mkdf-author-name {
            margin: 0;

            a {
                color: $default-heading-color;

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .mkdf-author-email {
            margin: 0;
        }

        .mkdf-author-text {
            @include mkdfRelativeHolderLayout();

            > p {
                margin: 12px 0 9px;
            }
        }

        .mkdf-author-social-icons {
            @include mkdfRelativeHolderLayout();

            a {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                padding: 0;
                margin: 0 7px 0 0;
                font-size: 14px;
                color: #b8bfc1;

                &:last-child {
                    margin: 0;
                }

                &:hover {
                    color: $first-main-color;
                }
            }
        }
    }
}

/* Single author box styles - end
	====================================================================== */