/* Related posts styles - begin
	====================================================================== */
.mkdf-like-posts-enabled {
    .mkdf-related-posts-holder {
        width: calc(100% - 110px);
        float: right;
    }
}

.mkdf-related-posts-holder {
    position: relative;
    width: 100%;
    float: none;
    display: inline-block;
    vertical-align: middle;
    padding: 0;
    box-sizing: border-box;

    .mkdf-related-posts-holder-inner {
        padding: 46px 0 26px;
    }

    .mkdf-related-posts-title {
        margin: 0 0 25px;

        > * {
            margin: 0;
        }
    }

    .mkdf-related-posts-inner {
        margin: 0 -7px;
    }

    .mkdf-related-post {
        position: relative;
        display: inline-block;
        float: left;
        width: 25%;
        margin: 0 0 30px;
        padding: 0 7px;
        box-sizing: border-box;
        cursor: pointer;

        .mkdf-related-post-inner {
            background-color: #fff;
            border: 1px solid $default-border-color;
            @include mkdfTransition(box-shadow .3s ease-in-out);
        }
    
        .mkdf-content-has-sidebar & {
            widtH: 33.33333333333333%;
        }
    
        .touch & {
            cursor: pointer;
        }
    
        &:hover {
            .mkdf-related-post-inner {
                box-shadow: 0px 1px 23.28px 0.72px rgba(255, 51, 102, 0.07);
            }

        }
    
        .mkdf-related-post-image {
            @include mkdfRelativeHolderLayout();
            overflow: hidden;
        
            a {
                position: relative;
                display: block;
            }
        
            img {
                display: block;
            }
        }

        .mkdf-related-post-content {
            padding: 20px 26px 30px;
            h4 {
                margin: 12px 0 0;
            }

            .mkdf-post-info-date {
                .mkdf-post-info-icon {
                    position: relative;
                    display: inline-block;
                    top: 2px;
                    color: $additional-text-color;
                    font-size: 16px;
                    margin-right: 3px;
                }

                > a {
                    color: $additional-text-color;
                    font-weight: 400;
                    letter-spacing: -0.02em;
                    &:hover {
                        color: $first-main-color;
                    }

                }
            }


        }
    }
}

/* Related posts styles - end
	====================================================================== */
