/* ==========================================================================
   Blog Single style - begin
   ========================================================================== */

.mkdf-blog-holder.mkdf-blog-single {
	
	article {
		margin: 0 0 26px;
		
		.mkdf-post-heading {
			margin-bottom: 54px;
		}

		
		.mkdf-post-info-top {
			float: left;
			width: 110px;
			margin-bottom: 7px;
		}

		.mkdf-like-posts-enabled & {

			.mkdf-post-text-main {
				float: right;
				width: calc(100% - 110px);

			}
		}

		.mkdf-post-text-main {
			float: none;
			width: 100%;

			.mkdf-post-title {
				margin: 0 0 24px 0;
			}

		}
		
		.mkdf-post-info-bottom {
			@include mkdfRelativeHolderLayout();
			margin: 31px 0 0;
			
			.mkdf-post-info-bottom-left,
			.mkdf-post-info-bottom-right {
				position: relative;
				display: inline-block;
				width: 50%;
				float: left;
			}
			
			.mkdf-post-info-bottom-left {

				.mkdf-tag-title {
					display: inline-block;
					margin: 0;
				}
				
				> div {
					position: relative;
					display: inline-block;
					color: $default-text-color;
					
					a {
						color: $additional-text-color;
						
						&:hover {
							color: $first-main-color;
						}
					}
				}
			}
			
			.mkdf-post-info-bottom-right {
				text-align: right;
				
				> div {
					display: inline-block;
					margin-left: 12px;
					
					&:first-child {
						margin-left: 0;
					}
				}

				.mkdf-post-info-date {
					.mkdf-post-info-icon {
						position: relative;
						display: inline-block;
						top: 2px;
						color: $additional-text-color;
						font-size: 16px;
						margin-right: 3px;
					}

					> a {
						color: $additional-text-color;
						font-weight: 400;
						letter-spacing: -0.02em;
						&:hover {
							color: $first-main-color;
						}

					}
				}

				.mkdf-blog-like {

					span,
					i {
						vertical-align: middle;
						color: $additional-text-color;
						&:first-child {
							margin-right: 7px;
							@include mkdfTransition(color 0.15s ease-out);
						}
					}

					.liked i {
						color: $first-main-color;
					}

					&:hover {

						i:first-child,
						span:first-child {
							color: $first-main-color;
						}
					}

					.mkdf-like {
						> span {
							position: relative;
							bottom: 2px;
						}
					}
				}

				.mkdf-post-info-comments-holder {
					span,
					i {
						vertical-align: middle;
						color: $additional-text-color;
					}

					.mkdf-post-info-icon {
						i {
							@include mkdfTransition(color 0.15s ease-out);
						}
						&:hover {
							i {
								color: $first-main-color;
							}
						}
					}

					.mkdf-post-info-comments {
						>span:not(.mkdf-post-info-icon) {
							position: relative;
							bottom: 3px;
						}
					}
				}

				.mkdf-post-info-category {
					.mkdf-post-info-icon {
						position: relative;
						display: inline-block;
						top: 2px;

						i {
							color: $default-text-color;
							@include mkdfTransition(color 0.15s ease-out);

							&:hover {
								i {
									color: $first-main-color;
								}
							}
						}
					}
					> a {
						color: $default-text-color;
						font-weight: 400;
						letter-spacing: -.02em;
						&:hover {
							color: $first-main-color;
						}
					}
				}
			}
		}

	//	Link Format

		&.format-link {

			.mkdf-link {
				padding: 7% 31px 25px 11%;
				margin-bottom: 33px;
				background-color: #fff;
				border: 1px solid #f8edf0;

				.mkdf-post-title {
					padding-right: 10%;
					margin: 0;
				}

				.mkdf-post-mark {
					position: relative;
					width: 100%;
					font-size: 36px;
					color: $first-main-color;
					text-align: right;
				}
			}
		}

		&.format-quote {

			.mkdf-quote {
				padding: 60px 31px 20px 11%;
				margin-bottom: 33px;
				background-color: #fff;
				border: 1px solid #f8edf0;

				.mkdf-post-quote-holder {
					.mkdf-post-title {
						padding-right: 10%;
						margin: 0 0 9px;
					}

					span {
						color: $additional-text-color;
						font-weight: 500;
					}
				}

				.mkdf-post-mark {
					position: relative;
					width: 100%;
					font-size: 36px;
					color: $first-main-color;
					text-align: right;
				}
			}
		}
	}
}
/* ==========================================================================
   Blog Single style - end
   ========================================================================== */